<template>
<div class="home-wrap">
    <router-link to="/catalog">demo</router-link>
    <router-link to="/userLogin">(node)</router-link>
    <div class="list" @click="toMyResume"></div>
    <div class="list" @click="toMyLottory"></div>
    <div class="list" @click="toMyTravel"></div>
</div>
</template>
 
<script>
import carousel from '../components/carousel'
import axios from 'axios'
export default{
    props: [''],
    components: {
        carousel,
    },
    
    data() {
        return {
            username: '', 
            password: '',
            repassword: '',
            userInfo: '',
            isShowRegisterPage: false, //
            isShowLoginPage: true, //
            isSuccessLoginPage: false, //
            link1: 'http://wangjiuhua.com/resume/index.html',
            link2: 'http://wangjiuhua.com:3000/',
            link3: 'http://wangjiuhua.com/travel/index.html'
        }
    },
    
    mounted(){
        // this.initData();
    },
    
    methods: {
        toMyResume() {
            window.location.href = this.link1
        },
        toMyLottory() {
            window.location.href = this.link2
        },
        toMyTravel() {
            window.location.href = this.link3
        }
    }
}
</script>
 
<style lang='less' scoped>
    .home-wrap{
        padding-top: 1rem;
        background: #fff;
        display: flex;
        flex-direction: column;
        font-size: 18px;
        a{
            margin-top: 20px;
            color: #000;
        }
        .list{
            margin-top: 20px;
        }
    }

</style>



